import React, { Component } from 'react';
import Modal from 'react-bootstrap/Modal'
import { get_icon } from '../lib/fs';

export default class Modal_status extends Component {
  constructor (props) {
    super(props)
    this.state = {
    }
  }
  render() {
    let {data}=this.props
    return (
      <React.Fragment>
        <Modal
          show={this.props.show}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          {!this.props.step&&<div className="d-flex justify-content-center">
            <div className="spinner-border" role="status">
              <span className="visually-hidden">Loading...</span>
            </div>
          </div>}
          {this.props.step&&<div className='container'>
          <div className='row'>
            <div className='col-12' style={{padding:"0px"}}>
              <div style={{textAlign:"center"}}>
                <div className='w-icon'>
                  <span className='icon-checkz'>{get_icon('check','62px','#33a938')}</span>
                </div>
                <h2 style={{paddingTop:"25px"}}>t hng thnh cng!</h2>
              </div>
            </div>
            <div className='col-12'>
              <p style={{textAlign: 'left', marginBottom: '8px', color: 'currentColor',marginTop: '12px'}}>Thng tin n hng :</p>
              <table style={{borderCollapse: 'collapse', width: "100%"}} className="tabz">
                 <thead> 
                    <tr style={{backgroundColor: 'burlywood'}}> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Tn sn phm</th> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Gi thnh</th> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>S lng</th> 
                      <th style={{backgroundColor: 'burlywood',color: 'black',fontSize: '14px',}}>Thnh tin</th>
                    </tr>
                  </thead>
                    <tbody> 
                      <tr>
                        <td data-column="Tn sn phm" style={{backgroundColor: 'white'}}><a style={{color: '#333',fontSize: '14px',backgroundColor: 'white',}}>{data.sp.name}<span style={{marginBottom: '0px', display: 'block',}}> Kch thc : {data.sp.attributes_kt}</span><span style={{marginBottom: '7px', display: 'block',}}> Mu : {data.sp.attributes_ms}</span></a>
                        </td>
                        <td data-column="Gi" style={{color: '#333',fontSize: '14px',backgroundColor: 'white'}}>{data.sp.price}</td>
                        <td data-column="S lng" style={{color: '#333',fontSize: '14px',backgroundColor: 'white'}}>{data.sp.quantity}</td>
                        <td data-column="Thnh tin" style={{color: '#333',fontSize: '14px',backgroundColor: 'white'}}>{(Number(data.sp.quantity)*Number(data.sp.price)).format(0, 3, '.', ',')} </td>
                      </tr>
                      <tr> 
                        <td></td>
                        <td></td>
                        <td style={{fontWeight: '600',fontSize: '14px'}}>Tng tin: </td> 
                        <td style={{fontWeight: '600', color: 'blue',fontSize: '14px',}}>{(Number(data.sp.quantity)*Number(data.sp.price)).format(0, 3, '.', ',')} </td>
                      </tr> 
                    </tbody>
              </table>
              <div className='showzs'>
                <p>Thng tin ngi nhn: </p>
                <p>Tn: <b>{data.user.z_name}</b></p>
                <p>a ch: <b>{data.user.z_address}</b></p>
                <p>in thoi: <b>{data.user.z_phone}</b></p>
                <p>Ghi ch: <b> {data.sp.z_note}</b></p>
              </div>
              <div class="footer-pp"> 
                <p class="bnt-home" onClick={()=>this.props.onHide()}>ng</p>
              </div>
            </div>
          </div>
        </div>}


        </Modal>
      </React.Fragment>
    );
  }
}

